var test = require('ava')


function swap (arr) {


    for ( var i = 0, piv = arr.length - 1 ; ; i < arr.length) {

        if ( arr[i] < arr[piv]){
            i++ 
        }
        else {
            var cur = arr[i]
            // var purp = arr[piv]
            arr[i] = arr[piv - 1] 
            arr[piv - 1] = arr[piv]
            arr[piv] = cur
            piv -= 1
        }
        console.log(arr);
    }
    return arr
}

test('swaps pivot to correct point', t => {

    var arr = [ 1, 8, 2, 9, 7, 4 ]
    var act = swap(arr)
    var exp = [ 1, 2, 4, 9, 7, 8]

    t.deepEqual(act, exp)

})
